<template>
    <div
    id="articleModal"
    ref="modal"
    class="modal fade"
    aria-labelledby="articleModalLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-lg">
      <div class="modal-content border-0">
        <div class="modal-header bg-primary text-white">
          <h5 id="articleModalLabel" class="modal-title">
            <span></span>
          </h5>
          <button
            type="button"
            class="btn-close btn-close-white"
            data-bs-dismiss="modal"
            aria-label="Close">
          </button>
        </div>
        <div class="modal-body">
          <div class="row g-2 mb-2">
            <div class="col-12">
              <label for="title" class="form-label"></label>
              <input id="title" type="text" class="form-control" placeholder=""
              v-model="tempArticle.title">
            </div>
            <div class="col-12">
              <label for="subtitle" class="form-label"></label>
              <input id="subtitle" type="text" class="form-control" placeholder=""
              v-model="tempArticle.description">
            </div>
          </div>
          <div class="row g-2">
            <div class="col-6">
              <div class="mb-3">
                <label for="imageUrl" class="form-label"></label>
                <div class="input-group mb-2">
                  <input
                    type="text"
                    class="form-control imageInput"
                    placeholder=" URL "
                    ref="imageUrl">
                  <button
                    type="button"
                    class="btn btn-outline-light"
                    @click="imageUpload('imageUrl')">
                    
                  </button>
                </div>
                <div class="input-group">
                  <input
                    class="form-control imageInput"
                    type="file"
                    id="fileInput"
                    ref="fileInput">
                  <button
                    type="button"
                    class="btn btn-outline-light"
                    @click="imageUpload('imageFile')">
                    
                  </button>
                </div>
              </div>
              <div class="image-cover border rounded-2" :style="{backgroundImage: `url(${tempArticle.image})`}"></div>
            </div>
            <div class="col-6 d-flex flex-column justify-content-between">
              <div class="mb-2">
                <label for="tag" class="form-label"></label>
                <input id="tag" type="text" class="form-control" placeholder=" [ ENTER ] "
                  v-model="tagValue" @keyup.enter="addTag(tagValue)">
              </div>
              <div class="d-flex flex-wrap mb-1">
                <button type="button" class="d-flex align-items-center btn btn-gray btn-sm rounded-3 me-1 mb-1"
                  v-for="(tag, index) in tempArticle.tag" :key="tag">
                  <span>{{ tag }}</span>
                  <i class="material-icons-round fs-3 ms-1" @click="removeTag(index)">clear</i>
                </button>
              </div>
              <div class="mb-2">
                <label for="author" class="form-label"></label>
                <input id="author" type="text" class="form-control" placeholder=""
                  v-model="tempArticle.author">
              </div>
              <div>
                <label for="create_at" class="form-label"></label>
                <input id="create_at" type="date" class="form-control"
                  v-model="tempArticle.createTime">
              </div>
            </div>
          </div>
          <hr>
          <div class="mb-2">
            <!-- ckeditor -->
            <ckeditor :editor="editor" :config="editorConfig" v-model="tempArticle.content"></ckeditor>
          </div>
          <div class="form-check me-3">
            <input
              id="isPublic"
              class="form-check-input"
              type="checkbox"
              v-model="tempArticle.isPublic"
              :true-value="true"
              :false-value="false">
            <label class="form-check-label" for="isPublic"></label>
          </div>
        </div>
        <div class="modal-footer">
          <button
            type="button"
            class="btn btn-outline-light"
            data-bs-dismiss="modal">
            
          </button>
          <button
            type="button"
            class="btn btn-primary"
            @click="updateArticle(tempArticle.id)">
            
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import modalMixin from '@/mixins/modalMixin'
import ClassicEditor from '@ckeditor/ckeditor5-build-classic'
import MyUploadAdapter from '@/methods/uploadAdapter'

// UploadAdapterPlugin
function MyCustomUploadAdapterPlugin (editor) {
  editor.plugins.get('FileRepository').createUploadAdapter = (loader) => {
    // Configure the URL to the upload script in your back-end here!
    return new MyUploadAdapter(loader)
  }
}

export default {
  props: ['temp-article', 'is_new'],
  data () {
    return {
      tagValue: '',
      imageInput: '',
      editor: ClassicEditor,
      editorConfig: {
        toolbar: ['heading', 'bold', 'italic', 'blockquote', 'link', '|', 'numberedList', 'bulletedList', '|', 'undo', 'redo'],
        extraPlugins: [MyCustomUploadAdapterPlugin]
      }
    }
  },
  mixins: [modalMixin],
  methods: {
    updateArticle (articleId) {
      //  DOM  ArticleModal  props 
      //  nextTick()  DOM 
      this.$nextTick(() => {
        let url = `${process.env.VUE_APP_URL}/api/${process.env.VUE_APP_PATH}/admin/article/${articleId}`
        let httpMethod = 'put'
        let messageStatus = ''

        if (this.is_new === true) {
          url = `${process.env.VUE_APP_URL}/api/${process.env.VUE_APP_PATH}/admin/article`
          httpMethod = 'post'
          messageStatus = ''
        }

        this.$http[httpMethod](url, { data: this.tempArticle })
          .then((res) => {
            this.$httpMessageState(res, messageStatus)
            this.hideModal()

            //  
            this.$emit('get-articles')
          })
          .catch((err) => {
            this.$httpMessageState(err.response, messageStatus)
          })
      })
    },
    addTag (tagValue) {
      this.tempArticle.tag.push(tagValue)
      this.tagValue = ''
    },
    removeTag (index) {
      this.tempArticle.tag.splice(index, 1)
    },
    imageUpload (imageStatus) {
      if (imageStatus === 'imageUrl') { // 
        this.tempArticle.image = (this.imageInput[0].value)
        this.imageInput[0].value = ''
      } else if (imageStatus === 'imageFile') { // 
        const file = this.imageInput[1].files[0]

        //  formData
        const formData = new FormData()
        formData.append('file-to-upload', file)

        const url = `${process.env.VUE_APP_URL}/api/${process.env.VUE_APP_PATH}/admin/upload`
        this.$http
          .post(url, formData)
          .then((res) => {
            this.tempArticle.image = (res.data.imageUrl)

            this.$httpMessageState(res, '')
            this.imageInput[1].value = ''
          })
          .catch((err) => {
            this.$httpMessageState(err.response, '')
          })
      }
    }
  },
  watch: {
    tempArticle () {
      //  YYYY-MM-DD
      const createTime = new Date(this.tempArticle.create_at * 1000).toISOString().split('T')[0]
      this.tempArticle.createTime = createTime

      //  API  itemArticle 
      if (this.is_new === 'editPublic') {
        this.tempArticle.isPublic = !(this.tempArticle.isPublic)
      }
    }
  },
  mounted () {
    this.imageInput = [...document.querySelectorAll('.imageInput')] // 
  }
}
</script>

<style lang="scss">
/* ckeditor link  Modal  focus  */
:root {
    --ck-z-default: 100;
    --ck-z-modal: calc( var(--ck-z-default) + 999 );
}
</style>
